@import "mixins";
@import "normalize";
@import "utilities";


html, body {
  height: 100%;
}

html {
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
  font-size: 13px;
}

.col-va {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items:center;
}

.col-va-s {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.row-b {
  display: flex;
  justify-content: space-around;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  background: url(../img/swiper-wrapper-bg.png) no-repeat 50% 0%;
  background-image: image-set(url(../img/swiper-wrapper-bg.png) 1x, url(../img/swiper-wrapper-bg@2x.png) 2x);
  -webkit-background-size: 100%;
  background-size: 100%;
}

.swiper-slide.has-arrow:after {
  content: '';
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  margin: auto;
  width: 18px;
  height: 10px;
  background: url(../img/nav-arrow@2x.png) no-repeat;
  background-size: 18px 10px;
}

$word-animation-duration: 2s;
$word-animation-duration-minus: .5s;
#slide-1.swiper-slide-active {
  p {
    letter-spacing: 3px;
  }
  .w1, .w2, .w3 {
    animation: fade-out $word-animation-duration ease-out both;
  }
  .w1 {
    animation-delay: 0;
  }
  .w2 {
    animation-delay: $word-animation-duration - $word-animation-duration-minus;
  }
  .w3 {
    animation-delay: $word-animation-duration*2 - $word-animation-duration-minus;
  }

  .w4 {
    position: relative;
    display: inline-block;
    // height: 20px;
    width: 3em;
    span {
      $recycle-time: 3s;
      position: absolute;
      left: 0;
      right: 0;
      top: -15px;
      text-align: center;
      animation: 12s infinite linear $word-animation-duration*2 - $word-animation-duration-minus both;
      &:nth-child(1) {
        animation-name: fade-41;
      }
      &:nth-child(2) {
        animation-name: fade-42;
      }
      &:nth-child(3) {
        animation-name: fade-43;
      }
      &:nth-child(4) {
        animation-name: fade-44;
      }
    }

  }

}
#slide-1 .slide-next {
  // animation: fadeout .5s 5.5s both;
}

@keyframes fade-out {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fade-41 {
  0% { opacity: 0; }
  12.5% { opacity: 1;}
  25% { opacity: 0;}
  100% { opacity: 0;}
}
@keyframes fade-42 {
  0% {opacity: 0;}
  25% { opacity: 0; }
  37.5% { opacity: 1;}
  50% { opacity: 0;}
  100% {opacity: 0;}
}
@keyframes fade-43 {
  0% {opacity: 0;}
  50% { opacity: 0; }
  62.5% { opacity: 1;}
  75% { opacity: 0;}
  100% {opacity: 0;}
}
@keyframes fade-44 {
  0% {opacity: 0;}
  75% { opacity: 0; }
  87.5% { opacity: 1;}
  100% { opacity: 0;}
}

#slide-2 a {
  color: #000 !important;
  text-decoration: none !important;
  width: 45%;
  text-align: center;
}
#slide-2 img {
  display: block;
  margin-bottom: 6px;
  width: 100%;
  height: auto;
  margin: auto;
}

#slide-2 .row-b {
  text-align: center;
}


.slide-1-intro {
  font-size: 14px;
  padding-top: 33px;
  background: url(../img/slide-intro-deco@2x.png) no-repeat 50% 0;
  background-size: 28px 26px;
  text-align: center;
}

.slide-2-intro {
  padding-top: 43px;
  background: url(../img/slide-intro-deco@2x.png) no-repeat 50% 0;
  background-size: 28px 26px;
  text-align: center;
}

#slide-3,
#slide-4,
#slide-5,
#slide-6 {
  p {
    text-align: center;
    position: relative;
    z-index: 10;
    margin-top: 30px;
    text-shadow: 0 0 20px rgba(255,255,255,.5);
    background: rgba(255,255,255,.7);
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .gallery-example {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-position: 50% 20px;
    background-repeat: repeat-y;
    transform: translateZ(0);
  }
  &.swiper-slide-active .gallery-example {
    animation: 25s linear infinite both;
  }
}

#slide-3 {
  .gallery-example {
    background-image: url(../img/show-example1.png);
    background-image: image-set(url(../img/show-example1.png) 1x, url(../img/show-example1@2x.png) 2x);
  }
  &.swiper-slide-active .gallery-example {
    animation-name: gallery-example-show3;
  }
}


#slide-4 {
  .gallery-example {
    background-image: url(../img/show-example2.png);
    background-image: image-set(url(../img/show-example2.png) 1x, url(../img/show-example2@2x.png) 2x);
  }
  &.swiper-slide-active .gallery-example {
    animation-name: gallery-example-show4;
  }
}

#slide-5 {
  .gallery-example {
    background-image: url(../img/show-example3.png);
    background-image: image-set(url(../img/show-example3.png) 1x, url(../img/show-example3@2x.png) 2x);
  }
  &.swiper-slide-active .gallery-example {
    animation-name: gallery-example-show5;
  }
}

#slide-6 {
  .gallery-example {
    background-image: url(../img/show-example4.png);
    background-image: image-set(url(../img/show-example4.png) 1x, url(../img/show-example4@2x.png) 2x);
  }
  &.swiper-slide-active .gallery-example {
    animation-name: gallery-example-show6;
  }
}


@keyframes gallery-example-show3 {
  0% { background-position: 50% 0;}
  100% { background-position: 50% -734px; }
}
@keyframes gallery-example-show4 {
  0% { background-position: 50% 0;}
  100% { background-position: 50% -741px; }
}
@keyframes gallery-example-show5 {
  0% { background-position: 50% 0; }
  100% { background-position: 50% -707px; }
}
@keyframes gallery-example-show6 {
  0% { background-position: 50% 0;}
  100% { background-position: 50% -735px; }
}


.n-btn {
  position: absolute;
  z-index: 100;
  bottom: 30px;
  border-radius: 10px;
  color: #fff;
  text-decoration: none;
  background: rgba(0,0,0,.9);
  width: 200px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-size: 12px;
  line-height: 30px;
  height: 30px;
  box-shadow: 0 0 10px rgba(255,255,255,.35);
}



.swiper-slide-active .n-btn.anime {
  animation: fadeout .5s 2s both;
}

@keyframes fadeout {
  0% {opacity: 0;transform: translate(0, 20px)}
  100% {opacity: 1;transform: translate(0, 0)}
}
